form {
    width: 90%;
    max-width: 300px;
    background-color: rgb(26, 25, 25);
    padding: 33px 40px;
    display: flex;
    align-content: center;
    flex-direction: column;
    border-radius:10px;
}

label {
    text-align: center;
    font-weight: bold;
    color: #ffffff;
    margin-bottom: 26px;
    font-size:15px;
}

textarea {
    background-color: #F9FBFC;
    border: 2px, solid #202329;
    border-radius: 2px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    color: #1E1E1F;
    font-size: 14px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100%-25px);
    
}

textarea::placeholder {
color: #797979;
font-family: 'Montserrat';
font-weight: 400;
}

textarea:focus {
    outline-color: #040404;
}

.TodoForm-buttonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.TodoForm-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;
    font-family: 'Montserrat';
}

.TodoForm-button-add {
    background-color: #f4ff54;
    box-shadow: 0px 5px 25px rgba(74, 75, 75, 1.5);
}

.TodoForm-button-cancel {
    background: transparent;
    color:rgb(255, 255, 255);
}
